import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Switch </h1>
                <blockquote className='page-tip'>
                    <br/>
                    /
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo1 />
                    </Label>
                    <Label className='code-box'>
                        <Demo3 />
                    </Label>
                    <Label className='code-box'>
                        <Demo5 />
                    </Label>
                </Label>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo2 />
                    </Label>
                    <Label className='code-box'>
                        <Demo4 />
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Switch</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'value', desc: '', type: 'boolean', default: 'false'},
                    {param: 'disabled', desc: '', type: 'boolean', default: 'false'},
                    {param: 'onChange', desc: '', type: 'function', default: ''},
                    {param: 'size', desc: 'switch small', type: 'string', default: ''},
                    {param: 'checkedText', desc: '', type: 'string', default: ''},
                    {param: 'unCheckedText', desc: '', type: 'string', default: ''},
                    {param: 'name', desc: 'formname', type: 'string', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>toggleSwitch()</i>
                        <span>Switch</span>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



